<template>
  <div>
    <nav class="navbar-fixed">
      <div class="bx f">
        <h1>
          <img
            src="../assets/icon_dapp@3x.png"
            v-if="lang=='cn'"
          >
          <img
            src="../assets/icon_dapp@3x.png"
            style="height:30px;"
            v-if="lang=='en'"
          >
        </h1>
        <ul class="f nav hidden-xs-only">
          <li>
            <a
              @click="changeLan('cn')"
              href="JavaScript:;"
              v-if="language == 'en'"
            >
              <img
                class="lan-img"
                src=""
              >
            </a>
            <a
              @click="changeLan('en')"
              href="JavaScript:;"
              v-else
            >
              <img
                class="lan-img"
                src=""
              >
            </a>
          </li>
          <li>
            <a
              @click="login"
              v-if="!account.name"
              href="JavaScript:;"
            >{{loginLan}}</a>
            <div
              class="account-cell"
              v-else
              href="JavaScript:;"
            >
              <span>{{account.name}}</span>
              <font-awesome-icon
                class="icon-logout"
                @click="logout"
                icon="sign-out-alt"
              />
            </div>
          </li>
        </ul>

        <div class="mobile-nav hidden-sm-and-up">
          <span
            class="login-name"
            v-if="account.name"
          >{{account.name}}</span>
          <span
            class="login-name"
            v-else
            @click="login"
          >{{loginLan}}</span>
          <div class="three col">
            <div
              :class="navbar?'hamburger is-active':'hamburger'"
              @click.stop="menuClick"
            >
              <span class="line"></span>
              <span class="line"></span>
              <span class="line"></span>
            </div>
          </div>
        </div>
      </div>
    </nav>

    <transition name='fade'>
      <div
        class="meun-mobile hidden-sm-and-up"
        v-if="navbar"
      >
        <ul>
          <li>
            <a
              @click="changeLan('cn')"
              href="JavaScript:;"
              v-if="language == 'en'"
            >中文</a>
            <a
              @click="changeLan('en')"
              href="JavaScript:;"
              v-else
            >English</a>
          </li>
          <li>
            <a
              @click="login"
              v-if="!account.name"
              href="JavaScript:;"
            >{{loginLan}}</a>
            <a
              v-else
              @click="logout"
              href="JavaScript:;"
            >{{logoutLan}}</a>
          </li>
        </ul>
      </div>
    </transition>

  </div>
</template>

<script>
import network from '@/utils/network';
import eventHub from '@/utils/event';

export default {
  methods: {
    changeLan (lan) {
      this.navbar = false
      this.$i18n.locale = lan
      this.language = lan
      this.$store.state.lang = lan
    },
    menuClick () {
      this.navbar = !this.navbar
    },
    getQueryString (name) {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      return null;
    },
    login () {
      if (this.$store.state.scatter) {
        this.$store.state.scatter.getIdentity({
          accounts: [network]
        }).then(() => {
          let account = this.$store.state.scatter.identity.accounts.find(account => account.blockchain === 'eos');
          if (!account) return
          this.$store.commit('UPDATE_ACCOUNT', account)
          this.$message({
            showClose: true,
            message: this.$t('common.login_success'),
            type: 'success',
            duration: 1500
          });
        }).catch(e => {
          this.$message({
            showClose: true,
            message: e.message,
            type: 'warning',
            duration: 1500
          })
        })
      }
    },

    logout () {
      this.navbar = false
      this.$store.state.scatter.forgetIdentity().then(() => {
        this.$message({
          showClose: true,
          message: this.$t('logout_success'),
          type: 'success',
          duration: 1500
        });
        this.$store.commit('UPDATE_ACCOUNT', {});
      });
    },

    showAbout () {
      this.navbar = false
      eventHub.$emit('SHOW_ABOUT');
    },

    showSocial () {
      this.navbar = false
      eventHub.$emit('SHOW_SOCIAL');
    },

    showReferrals () {
      this.navbar = false
      eventHub.$emit('SHOW_REFERRALS');
    },
    showCustomSeed () {
      this.navbar = false
      eventHub.$emit('SHOW_CUSTOMSEED');
    }
  },

  data () {
    return {
      navbar: false,
      language: 'cn'
    };
  },

  computed: {
    account () {
      return this.$store.state.account;
    },
    scatter () {
      return this.$store.state.scatter
    },
    loginLan () {
      return this.$t('login')
    },
    logoutLan () {
      return this.$t('logout')
    },
    howPlay () {
      return this.$t('how_to_play')
    },
    referrals () {
      return this.$t('referrals')
    },
    fairness () {
      return this.$t('fairness')
    },
    social () {
      return this.$t('social')
    },
    lang () {
      return this.$store.state.lang
    }
  },
  mounted () {
    this.$i18n.locale = this.$lan('lang')
    this.language = this.$lan('lang')
    this.$store.state.lang = this.language
    eventHub.$on('HIDDEN_MENU', () => this.navbar = false)
  }
}
</script>

