import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    account: {},
    lang: 'cn'
  },

  mutations: {
    UPDATE_ACCOUNT(state, account) {
      state.account = account;
      state.account.name = account.name.toString()
    },
    UPDATE_SCATTER(state, obj) {
      state.scatter = obj
    }
  }
});
