<template>
  <main id="app">
    <dice-body />
  </main>
</template>

<script>
import ScatterJS from 'scatterjs-core'
import ScatterEOS from 'scatterjs-plugin-eosjs'
import network from '@/utils/network';
export default {
  mounted () {
    this.getScatter()
  },
  methods: {
    getScatter () {
      ScatterJS.plugins(new ScatterEOS())
      ScatterJS.scatter.connect('exchange').then(connected => {
        console.log("akjskldjaklsjd", connected)
        if (!connected) return false
        const scatter = ScatterJS.scatter
        this.$store.commit('UPDATE_SCATTER', scatter)
        let account
        if (scatter.identity) {
          account = scatter.identity.accounts.find(account => account.blockchain === 'eos')
          this.$store.commit('UPDATE_ACCOUNT', account)
        }
        if (!account || !account.name) {
          this.login()
        }
      })
    },
    login () {
      if (this.scatter) {
        this.scatter.getIdentity({
          accounts: [network]
        }).then(() => {
          let account = this.scatter.identity.accounts.find(account => account.blockchain === 'eos');
          if (!account) return
          this.$store.commit('UPDATE_ACCOUNT', account)
          this.$message({
            showClose: true,
            message: this.$t('common.login_success'),
            type: 'success',
            duration: 1500
          });
        }).catch(e => {
          this.$message({
            showClose: true,
            message: e.message,
            type: 'warning',
            duration: 1500
          })
        })
      }
    }
  },
  components: {
    diceHeader: require('@/components/header').default,
    diceBody: require('@/components/body').default
  },
  computed: {
    scatter () {
      return this.$store.state.scatter
    }
  }
};
</script>

